* {
  	scrollbar-width: none;
}
html::-webkit-scrollbar {
    width: 0px;
    background: transparent;
}
body {
	margin: 0;
	padding: 0;
}
.icon-contribution {
	position: fixed;
	bottom: 0;
	left: 50%;
	transform: translateX(-50%);
	font-size: 11px;
}
.total-width {
	width: 100vw;
	height: 100vh;
	text-align: center;
	overflow: hidden;
}
.drawer-button-container {
	z-index: 1000;
	position: fixed;
	top: 5px;
	left: 5px;
	display: none;
}
.full-height {
	position: relative;
	height: 80vh;
	margin-top: 7vh;
	overflow-y: scroll;
  	scrollbar-width: none;
}
.theme-button-container {
	z-index: 1000;
	position: fixed;
	top: 5px;
	right: 5px;
	padding: 5px;
}
.input-container {
	position: absolute;
	left: 0;
	right: 0;
	bottom: 0;
	display: flex;
	align-items: center;
}
.users-container {
	height: 30vh;
	overflow-y: scroll;
	scrollbar-width: none;
}
.siv {
	display: none;
}
@media (max-width: 400px){
	.vis {
		display: none;
	}
	.siv {
		display: block;
	}
	.drawer-button-container {
		display: block;
	}
	.full-height {
		height: 70vh;
	}
	.icon-contribution {
		display: none;
	}
}